@import url('https://fonts.googleapis.com/css?family=Pacifico');

.footerContainer {
  background-color: #fff;
  height: 70px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: flex-center;
      align-items: flex-center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  max-width: 1440px;
}

.submitMessage {
  cursor: pointer;
  background-color: #41626d;
  width: 47px;
  height: 37px;
  border-radius: 5px;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 8px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.input-text {
  margin-left: 10px;
  margin-bottom: 8px;
  border: 1px solid black;
  width: 100%;
  height: 30px;
  border-radius: 5px;
  font-size: 20px;
}

.input-text:focus {
  outline-width: 0;
}

.send {
  color: #cfecd8;
}

@media only screen and (min-device-width: 768px) {
  .footerContainer {
    margin-left: 200px;
  }
};



